export default function Input(props) {
  return (
    <div className="Input">
      <input type="text" />
      <style jsx>{`
        input {
          width: 100%;
          font-size: 24px;
          color: rgb(74, 74, 74);
          background-color: rgb(242, 243, 244);
          text-align: right;
          font-family: inherit;
          font-weight: 700;
          height: 52px;
          border-radius: 5px;
          border: none;
        }
      `}</style>
    </div>
  )
}